*{
    
    background-color: #393939;
    color: #e7e8eb;
}
.todo-title {
    height: 60px;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 2.5em;
    font-weight: 600;
    color: #f48024;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
    margin-bottom: 10px;
}
.todo-title>img{
    height: 50px;
    width: 50px;
    margin-left: 10px;
}

.todo-form {
    height: 60px;
    margin-top: 10px;
    margin-bottom: 10px;
    display: flex;
    justify-content: center;
    background-color: #e7e8eb;
}

.todo-form > form {
    width: 100vw;
    display: flex;
    justify-content: center;
}

.todo-form > form > input {
    width: 50% ;
    height: 40px;
    border-radius: 5px;
    border: 2px solid #3498db;
    font-size: 1.3em;
    font-weight: 500;
    padding: 0 10px;
    
    
}
.todo-form > form > input:focus {
    outline: none;
    border:2px solid #2ecc71;
    box-shadow: 0 15px 25px rgba(0,0,0,0.5);
}

.todo-form > form > input::placeholder {
    color: #e7e8eb;
}

.todo-items {
    width: 65%;
    height: auto;
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

@media only screen and (max-width: 700px) {
    .todo-items {
        width: 100%;
      background-color: lightblue;
    }

    .todo-form > form > input {
        width: 80%;
    }
  }

.item {
    position: relative;
    height: 60px;
    width: 80%;
    border-radius: 2px;
    font-size: 1.5rem;
    color: rgb(58, 58, 58);
    margin: 10px auto;
    text-transform: capitalize;
    padding:0 10px;
    display: flex;
    align-items: center;
    box-shadow: 0 -2px 10px rgba(0, 0, 0, 1);

}
.item > img {
    width: 35px;
    height: 35px;
    position: absolute;
    right: 10px;
    cursor: pointer;   
}

.item > input {
    width: 20px;
    height: 20px;
    margin-right: 10px;
}
